<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide"><img :src=data[0] alt=""></div>
      <div class="swiper-slide"><img :src=data[1] alt=""></div>
      <div class="swiper-slide"><img :src=data[2] alt=""></div>
    </div>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
    import Swiper from "swiper"
    import "../../../node_modules/swiper/dist/css/swiper.min.css"

    export default {
        name: "conSwiper",
        mounted(){ //dom
          let mySwiper = new Swiper ('.swiper-container', {
            direction: 'horizontal', // 垂直切换选项
            loop: true, // 循环模式选项
            autoplay:true,
            // 如果需要分页器
            pagination: {
              el: '.swiper-pagination',
            },
          })
        },
        props:["data"]
    }

</script>

<style scoped>

  .swiper-container{
    overflow: initial;
  }
  .swiper-slide{

  }
  .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: -.44rem;
    left: -.05rem;
    width: 100%;
  }
</style>



