<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide"><img src="../../../static/imgs/my-02.png" alt=""></div>
      <div class="swiper-slide"><img src="../../../static/imgs/my-02.png" alt=""></div>
      <div class="swiper-slide"><img src="../../../static/imgs/my-02.png" alt=""></div>
      <div class="swiper-slide"><img src="../../../static/imgs/my-03.png" alt=""></div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
  import Swiper from "swiper"
  import "../../../node_modules/swiper/dist/css/swiper.min.css"
    export default {
        name: "myBanner",
        mounted(){ //dom
          var mySwiper = new Swiper('.swiper-container', {
            direction: 'horizontal', // 垂直切换选项
            loop: true, // 循环模式选项
            // 如果需要分页器
            spaceBetween:-80,
            pagination: {
              el: '.swiper-pagination',
            }
          })
        }
    }
</script>

<style scoped>
  .swiper-container{
    width: 3.75rem;
    height: 3.63rem;
    background: #f4f3ef;
    overflow: initial;
  }
  .swiper-slide img{
    width: 2.64rem;
    height: 3.63rem;
    margin: 0 auto;
  }
  .swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: -.7rem;
    width: 100%;
  }
</style>



