<template>
    <div id="news">
      <header>
        <tgp-my-nav></tgp-my-nav>
      </header>
      <section class="mycontent">
        <tgp-my-con></tgp-my-con>
        <tgp-my-banner></tgp-my-banner>
      </section>
      <footer>
          <tgp-world-news-footer></tgp-world-news-footer>
      </footer>
    </div>
</template>

<script>
  import TgpMyNav from "./TgpMyNav"
  import TgpMyCon from "./TgpMyCon"
  import TgpMyBanner from "./TgpMyBanner"
  import TgpWorldNewsFooter from "../tgpworld/TgpWorldNewsFooter"
    export default {
        name: "TgpMy",
        components: {TgpMyBanner, TgpWorldNewsFooter, TgpMyCon, TgpMyNav}
    }
</script>

<style scoped>
.mycontent{
  background: #f4f3ef;
}
</style>



