<template>
  <div class="tgp-world-news-footer">
    <router-link to="/"  @click="switchTo('/')" exact>
      <img :src="'/' === $route.path ? tabBarImgArr[0].selected : tabBarImgArr[0].normal" alt="">
      <span>忍界</span>
    </router-link>
    <router-link to="/TgpFindCon" @click="switchTo('/TgpFindCon')">
      <img :src="'/TgpFindCon' === $route.path ? tabBarImgArr[1].selected : tabBarImgArr[1].normal" alt="">
      <span>发现</span>
    </router-link>
    <router-link to="/TgpChat" @click="switchTo('/TgpChat')">
      <img :src="'/TgpChat' === $route.path ? tabBarImgArr[2].selected : tabBarImgArr[2].normal" alt="">
      <span>聊天</span>
    </router-link>
    <router-link to="/TgpGame" @click="switchTo('/TgpGame')">
      <img :src="'/TgpGame' === $route.path ? tabBarImgArr[3].selected : tabBarImgArr[3].normal" alt="">
      <span>游戏</span>
    </router-link>
    <router-link to="/TgpMy" @click="switchTo('/TgpMy')">
      <img :src="'/TgpMy' === $route.path ? tabBarImgArr[4].selected : tabBarImgArr[4].normal" alt="">
      <span>我</span>
    </router-link>
  </div>
</template>

<script>
    export default {
        name: "TgpWorldNewsFooter",
        methods :  {
            switchTo(path)
              {
                this.$router.replace(path)
              }
        },
        data(){
          return {
            tabBarImgArr:[   //图片切换
              {normal: require('../../../static/imgs/world-news-08-1.png'),
                  selected: require('../../../static/imgs/world-news-08.png')},
              {normal: require('../../../static/imgs/world-news-09.png'),
                  selected: require('../../../static/imgs/world-news-09-1.png')},
              {normal: require('../../../static/imgs/world-news-10.png'),
                  selected: require('../../../static/imgs/world-news-10-1.png')},
              {normal: require('../../../static/imgs/world-news-11.png'),
                   selected: require('../../../static/imgs/world-news-11-1.png')},
                {normal: require('../../../static/imgs/world-news-12.png'),
                   selected: require('../../../static/imgs/world-news-12-1.png')}
            ]
          }
      }
    }
</script>

<style scoped>
.router-link-active span{
  color: #08a1da;
}
</style>



