import Vue from 'vue'
import Router from 'vue-router'
import TgpWorldSpe from "../components/tgpworld/TgpWorldSpe";
import TgpWorldNews from "../components/tgpworld/TgpWorldNews";
import TgpWorldVid from "../components/tgpworld/TgpWorldVid";
import TgpWorldStr from "../components/tgpworld/TgpWorldStr";
import TgpWorldAnn from "../components/tgpworld/TgpWorldAnn";
import TgpFindCon from "../components/tgpfind/TgpFindCon";
import TgpChat from "../components/tgpchat/TgpChat";
import TgpGame from "../components/tgpgame/TgpGame";
import TgpMy from "../components/tgpmy/TgpMy";


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: TgpWorldNews
    },
    {
      path: '/TgpWorldSpe',
      component: TgpWorldSpe
    },
    {
      path: '/TgpWorldVid',
      component: TgpWorldVid
    },
    {
      path: '/TgpWorldStr',
      component: TgpWorldStr
    },
    {
      path: '/TgpWorldAnn',
      component: TgpWorldAnn
    },
    {
      path: '/TgpFindCon',
      component: TgpFindCon
    },
    {
      path: '/TgpChat',
      component: TgpChat
    },
    {
      path: '/TgpGame',
      component: TgpGame
    },
    {
      path: '/TgpMy',
      component: TgpMy
    }
  ]
})



