<template>
  <div id="news">
    <header>
      <tgp-world-news-header>

      </tgp-world-news-header>
    </header>
    <section>
      <div class="tgp-world-spe-con">
        <tgp-world-spe-cont v-if="cartInfo.world" :data="cartInfo.world.spe">

        </tgp-world-spe-cont>
        <tgp-world-spe-conb v-if="cartInfo.world" :data="cartInfo.world.spe">

        </tgp-world-spe-conb>
      </div>
    </section>
    <footer>
      <tgp-world-news-footer>

      </tgp-world-news-footer>
    </footer>
  </div>
</template>

<script>
  import TgpWorldNewsHeader from "./TgpWorldNewsHeader"
  import TgpWorldNewsFooter from "./TgpWorldNewsFooter"
  import TgpWorldSpeCont from "./TgpWorldSpeCont"
  import TgpWorldSpeConb from "./TgpWorldSpeConb"
  import cartApi from "../../Apis/cartApi"


  export default {
      name: "TgpWorldSpe",
      components: {
        TgpWorldSpeConb,
        TgpWorldSpeCont,
        TgpWorldNewsHeader,
        TgpWorldNewsFooter
      },
    data() {
      return {
        cartInfo: {}
      }
    },
    created() {
      this._initData()
    },
    methods: {
      _initData() {
        cartApi.getCartsInfo(data => {
          console.log(data)
          this.cartInfo = data;
        })
      }
    }
  }
</script>

<style scoped>

</style>



