              <a href="##"><img src="../../../static/imgs/world-news-06.png" alt=""><p>{{data.forum}}</p></a>
            </div>
          </div>
        </div>
        <div class="tgp-world-news-content-right">
          <a href="##"><img :src=data.imgs alt=""></a>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "TgpWorldNewsSectionCon",
        props:["data","idx"]
    }
</script>

