<template>
  <div id="news">
    <header>
      <tgp-world-news-header>

      </tgp-world-news-header>
    </header>
    <section>
      <tgp-world-vid-con v-if="cartInfo.world" :data="cartInfo.world.vid">

      </tgp-world-vid-con>
    </section>
    <footer>
      <tgp-world-news-footer>

      </tgp-world-news-footer>
    </footer>
  </div>
</template>

<script>
  import TgpWorldNewsHeader from "./TgpWorldNewsHeader"
  import TgpWorldNewsFooter from "./TgpWorldNewsFooter"
  import TgpWorldVidCon from "./TgpWorldVidCon"
  import cartApi from "../../Apis/cartApi"


    export default {
        name: "TgpWorldVid",
        components: {
          TgpWorldVidCon,
          TgpWorldNewsHeader,
          TgpWorldNewsFooter
        },
      data() {
        return {
          cartInfo: {}
        }
      },
      created() {
        this._initData()
      },
      methods: {
        _initData() {
          cartApi.getCartsInfo(data => {
            console.log(data)
            this.cartInfo = data;
          })
        }
      }
    }
</script>

<style scoped>

</style>



