<template>
  <div id="news">
    <header>
      <tgp-world-news-header>

      </tgp-world-news-header>
    </header>
    <section>
      <tgp-world-news-section v-if="cartInfo.world" :data="cartInfo.world.news">

      </tgp-world-news-section>
    </section>
    <footer>
      <tgp-world-news-footer>

      </tgp-world-news-footer>
    </footer>
  </div>
</template>

<script>
  import TgpWorldNewsHeader from "./TgpWorldNewsHeader"
  import TgpWorldNewsSection from "./TgpWorldNewsSection"
  import TgpWorldNewsFooter from "./TgpWorldNewsFooter"
  import cartApi from "../../Apis/cartApi"

  export default {
    name: "TgpWorldNews",
    components: {
      TgpWorldNewsHeader,
      TgpWorldNewsSection,
      TgpWorldNewsFooter
    },
    data() {
      return {
        cartInfo: {}
      }
    },
    created() {
      this._initData()
    },
      methods: {
      _initData() {
        cartApi.getCartsInfo(data => {
          console.log(data)
          this.cartInfo = data;
        })
      }
    }
  }
</script>

<style>
  body,html,h1,h2,h3,h4,h5,h6,ul,li,img,ol,dl,dd,p,input,select,textarea,td,div,form,textarea,th{
    margin: 0;
    padding: 0;
  }
  html{
    font-size: 26.67vw;
  }
  html,body{
    height: 100%;
  }
  body{
    font-family:"微软雅黑";
    display: -webkit-flex;
    -webkit-flex-direction: column;
  }
  a{
    text-decoration:none;
  }
  ul,ol{
    list-style:none;
  }
  img{
    border:0;
    display: block;
  }

</style>



